@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Google Font Link */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

#body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #5ac3e6;
  padding: 30px;
  overflow: hidden;
}

.container {
  position: relative;
  max-width: 1200px;
  width: 100%;
  background: #fff;
  height: 692px;
  padding: 40px 30px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  perspective: 2700px;
}

.container .cover {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 50%;
  z-index: 98;
  transition: all 1s ease;
  transform-origin: left;
  transform-style: preserve-3d;
}

.container #flip:checked ~ .cover {
  transform: rotateY(-180deg);
}

.container .cover .front,
.container .cover .back {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.cover .back {
  transform: rotateY(180deg);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.container .cover::before,
.container .cover::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  background: #5ac3e6;
  opacity: 0;
  z-index: 12;
}

.container .cover::after {
  /* opacity: 0.3; */
  transform: rotateY(180deg);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.container .cover img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: 10;
}

.container .cover .text {
  position: absolute;
  z-index: 130;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cover .text .text-1,
.cover .text .text-2 {
  font-size: 26px;
  font-weight: 600;
  color: #fff;
  text-align: center;
}

.cover .text .text-2 {
  font-size: 15px;
  font-weight: 500;
}

.container .forms {
  height: 100%;
  width: 100%;
  background: #fff;
}

.container .form-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-content .login-form,
.form-content .signup-form {
  width: calc(100% / 2 - 25px);
}

.form-content .login-form {
  margin-top: 20px;
}

.form-content .signup-form {
  margin-top: -70px;
}

.forms .form-content .title {
  position: relative;
  font-size: 24px;
  font-weight: 500;
  color: #333;
}

.forms .form-content .title:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  width: 25px;
  background: #5ac3e6;
}

.forms .signup-form .title:before {
  width: 20px;
}

.forms .form-content .input-boxes {
  margin-top: 30px;
}

.forms .form-content .input-box {
  display: flex;
  align-items: center;
  height: 50px;
  width: 100%;
  margin: 10px 0;
  position: relative;
}

.form-content .input-box input {
  height: 100%;
  width: 100%;
  outline: none;
  border: none;
  padding: 0 30px;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.form-content .input-box input:focus,
.form-content .input-box input:valid {
  border-color: #5ac3e6;
}

.form-content .input-box i {
  position: absolute;
  color: #5ac3e6;
  font-size: 17px;
}

.forms .form-content .text {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}

.forms .form-content .text a {
  text-decoration: none;
}

.forms .form-content .text a:hover {
  text-decoration: underline;
}

.forms .form-content .button {
  color: #fff;
  margin-top: 40px;
}

.forms .form-content .button input {
  color: #fff;
  background: #5ac3e6;
  border-radius: 6px;
  padding: 0;
  cursor: pointer;
  transition: all 0.4s ease;
}

.forms .form-content .button input:hover {
  background: black;
}

.forms .form-content label {
  color: black;
  cursor: pointer;
}

.forms .form-content label:hover {
  text-decoration: underline;
}

.forms .form-content .login-text,
.forms .form-content .sign-up-text {
  text-align: left;
  margin-top: 25px;
}

.container #flip {
  display: none;
}

/* Create two equal columns that floats next to each other */
.column {
  float: left;
  width: 50%;
  padding: 10px;
  height: 80px;
  /* Should be removed. Only for demonstration */
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.front .fImg {
  margin-left: -300px;
}

.back {
  background-color: blueviolet;
}

.back #lImg {
  width: 60px;
  height: 70px;
  margin-left: -180px;
  margin-top: 12px;
}

.back .bH3 {
  text-align: center;
  margin-top: 32px;
  color: white;
  margin-right: 80px;
}

.back .bH2 {
  text-align: center;
  margin-top: 32px;
  color: white;
}

.back #backImg {
  width: 600px;
  height: 451px;
  margin-top: 30px;
  margin-left: -300px;
}

table tr {
  float: left;
}

table .tImg {
  width: 60px;
  height: 70px;
}

table .tH3 {
  color: black;
}

.form-content .login-form .lfcontent {
  line-height: 2px;
  margin-top: -70px;
}

.form-content .login-form .lfcontent .title {
  float: left;
}

.form-content .login-form .lfcontent .lfH4 {
  float: left;
  margin-top: 50px;
  margin-left: -64px;
}

.form-content .login-form .lfH6 {
  float: left;
  margin-top: 20px;
}

.form-content .login-form form .input-boxes {
  margin-top: 140px;
}

.form-content .login-form form .input-boxes .text {
  font-size: 16px;
  margin-top: 12px;
}

.form-content .login-form form .input-boxes .text #test {
  float: left;
  margin-top: 5px;
}

.form-content .login-form form .input-boxes .text .formcontent {
  color: grey;
  text-decoration: none;
  float: left;
}

.form-content .login-form form .input-boxes .text .formcontent1 {
  color: #5ac3e6;
  text-decoration: none;
  float: right;
}

.form-content .login-form form .input-boxes .text sign-up-text {
  font-size: 16px;
}

.form-content .login-form form .input-boxes .text sign-up-text .formcontent2 {
  color: #5ac3e6;
  text-decoration: none;
}

@media (max-width: 730px) {
  .container .cover {
    display: none;
  }
  .form-content .login-form,
  .form-content .signup-form {
    width: 100%;
  }
  .form-content .signup-form {
    display: none;
  }
  .container #flip:checked ~ .forms .signup-form {
    display: block;
  }
  .container #flip:checked ~ .forms .login-form {
    display: none;
  }
}
